.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modalBox {
  position: fixed;
  top: calc(50% - 150px);
  left: calc(50% - 200px);
  width: 400px;
  height: 300px;
  overflow: hidden;
  background-color: rgba(53, 50, 47, 0.9);
  z-index: 101;
  border-radius: $mainBorder;
  border-radius: 15px;
  color: $thm-warmWhite;
  text-align: left;
  box-shadow: 0px 0px 6px 3px rgba(255, 255, 255, 0.452);
}

@media only screen and (max-width: 700px), (max-height: 550px) {
  .modalBox {
    width: 350px;
    height: 290px;
    top: calc(50% - 145px);
    left: calc(50% - 175px);
  }
}
